.sticky {
  position: sticky;
  top: 0;

  flex-direction: column;
  height: 80vh;

  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  //background-image: url( "../general/img/background.png" );
  //background-color: aqua;
}

.innerContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 80%;
  padding: 20px 20px;

  //background-color: magenta;

  .titleText {
    display: flex;
    flex: 1;
    width: 100%;
    // background-color: #FFF;
    align-items: center;
    font-weight: 700;

    span {
      font-size: 22px;
      text-align: left;
      color: #dcdcdc;
      line-height: 1.6;
      text-transform: uppercase;
      letter-spacing: 1px;

      strong {
        font-size: 16px;
        color: $mdoc-brand-blue;
        background-color: $mdoc-ui-blue;
      }
    }
  }
  .contentText {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex: 3;
    overflow: hidden;
    flex-wrap: wrap;
    //background-color: mediumorchid;

    span {
      display: inline-block;
      font-size: 30px;
      text-align: left;
      color: #000000;
      line-height: 1.3;
      text-transform: uppercase;
      letter-spacing: 2px;

      strong {
        font-size: 35px;
        color: #000;

        background-image: linear-gradient(#fff 50%, rgba(50, 80, 210, 0.2) 50%);
      }

      blu {
        font-size: 35px;
        font-weight: 700;
        color: $mdoc-brand-blue;
      }
    }
  }
}

