.card-frame {
  position: absolute;
  width: 95vw;
  max-width: $page-max-width;
  height: 30vh;
  bottom: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card {
  width: 24%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5vw;
  transform-style: preserve-3d;
  position: relative;
  transform: perspective(4000px) translate(100vw) rotate(180deg);
}

.front,
.back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1vw;
  overflow: hidden;
  backface-visibility: hidden;
}

.front {
  background-color: #fff;
  font-size: 0.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: 2px solid #e6edfa;
  .Img {
    display: flex;
    padding: 20px;
    margin-left: 15px;
    width: calc(100% - 60px);
  }
}

.back {
  transform: rotateY(180deg);
  background-color: #e6edfa;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  // background-image: repeating-linear-gradient(0deg, #3B3A38, #3B3A38 33.3%, #FFFFFF00 0px, #FFFFFF00 67%) ;
  background-size: 100%;
}

.Img {
  display: flex;
  padding: 10px;
  width: calc(100% - 60px);
}

.description {
  padding: 0px 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .first-line {
    display: flex;
  }

  span {
    font-size: 14px;
    text-align: center;
    color: rgb(38, 24, 24);
    line-height: 1.6;

    strong {
      font-size: 16px;
      color: $mdoc-brand-blue;
    }
  }
}

@media screen and (orientation: portrait) {
  .sticky {
    // position: sticky;
    // top: 0;

    // height: 100vh;
    // display: flex;
    // align-items:flex-end;
