    // background-image: url( "../general/img/background.png" );
  }

  .card-frame {
    display: grid;
    height: initial;
    width: initial;
    bottom: 10px;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    gap: 4vw;

    justify-items: center;
  }

  .card {
    width: 35vw;
    height: 45vw;
  }

  .front {
    background-color: #fff;

    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border: 1px solid #e6edfa;
