      display: flex;
      padding: 0px;
      margin-left: 5px;
      width: calc(100% - 60px);
    }
  }

  .Img {
    display: flex;
    padding: 0;
    width: calc(100% - 60px);
  }

  .description {
    padding: 0px 8px;

    .first-line {
      display: flex;
    }

    span {
